<template>
    <div class="chart-wrapper">
        <div id="chrt_user_form"></div>
    </div>
</template>

<script>
// import echarts from 'echarts'
import * as echarts from 'echarts'

export default {
    data() {
        return {
            chart: null,
            option: null
        }
    },
    mounted() {
        this.option = {
            legend: {
                orient: 'vertical',
                x: 'bottom',
                data: ['A', 'B', 'C', 'D', 'E']
            },
            series: [
                {
                type: 'pie',
                radius: ['50%', '70%'],
                avoidLabelOverlap: false,
                label: {
                    show: false,
                    position: 'center',
                    emphasis: {
                    show: true
                    }
                },
                labelLine: {
                    show: false
                },
                emphasis: {
                    label: {
                    show: true,
                    fontSize: '30',
                    fontWeight: 'bold'
                    }
                },
                data: [
                    { value: 335, name: 'A' },
                    { value: 310, name: 'B' },
                    { value: 234, name: 'C' },
                    { value: 135, name: 'D' },
                    { value: 1548, name: 'E' }
                ]
                }
            ]
        }
        this.chart = echarts.init(
            document.getElementById('chrt_user_form')
        )

        this.chart.setOption(this.option)
    }
}
</script>

<style>

</style>


