<template>
    <div class="chart-wrapper">
        <div id="chrt_money_statis"></div>
    </div>
</template>

<script>
// import echarts from 'echarts'
import * as echarts from 'echarts'

export default {
    data() {
        return {
            chart: null,
            option: null
        }
    },
    computed: {
        mon() { return [] }
    },
    mounted() {
        this.option = {
            xAxis: {
                data: ['A', 'B', 'C', 'D', 'E']
            },
            yAxis: { },
            series: [
                {
                data: [10, 22, 28, 23, 19],
                type: 'line',
                label: {
                    show: true,
                    position: 'bottom',
                    textStyle: {
                        fontSize: 20
                    }
                }
                }
            ]
        }
        this.chart = echarts.init(
            document.getElementById('chrt_money_statis')
        )

        this.chart.setOption(this.option)
    }
}
</script>

<style>

</style>


